import app from "../../../App.vue"
var db = wx.cloud.database().collection("userInfo")  
export default {
	data() {
		return {
			avatarUrl:"../../../static/logo.png",
			nickName:"未登录",
			gender:"",
			hasUserInfo: false,
			canIUse: wx.canIUse('button.open-type.getUserInfo'),
			userInfo:{},
			
			height: 64, //header高度
			top: 5, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,

			personInfo :[{
				nickName:"",  // 昵称
				age:"",			
				sex:"",  
				career:"",
				schoolData:'',
				provinceSchool:"",
				longitude:"", /* 用户当前位置*/
				latitude:"",
				location:"", 
			}],
			
			title: 'Hello',
			ColorList: this.ColorList,
			avatar: [
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			],
			cuIconList: [{
				cuIcon: 'cardboardfill',
				color: 'red',
				badge: 120,
				name: 'VR'
			}, {
				cuIcon: 'recordfill',
				color: 'orange',
				badge: 1,
				name: '录像'
			}, {
				cuIcon: 'picfill',
				color: 'yellow',
				badge: 0,
				name: '图像'
			}, {
				cuIcon: 'noticefill',
				color: 'olive',
				badge: 22,
				name: '通知'
			}, {
				cuIcon: 'upstagefill',
				color: 'cyan',
				badge: 0,
				name: '排行榜'
			}, {
				cuIcon: 'clothesfill',
				color: 'blue',
				badge: 0,
				name: '皮肤'
			}, {
				cuIcon: 'discoverfill',
				color: 'purple',
				badge: 0,
				name: '发现'
			}, {
				cuIcon: 'questionfill',
				color: 'mauve',
				badge: 0,
				name: '帮助'
			}, {
				cuIcon: 'commandfill',
				color: 'purple',
				badge: 0,
				name: '问答'
			}, {
				cuIcon: 'brandfill',
				color: 'mauve',
				badge: 0,
				name: '版权'
			}],
			modalName: null,
			gridCol:4,
			gridBorder: true,
			menuBorder: true,
			menuArrow: true,
			menuCard: true,
			skin: true,
			listTouchStart: 0,
			listTouchDirection: null,
		}
	}, 
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	},
	
	onLoad(e) {

		// 读取数据库 获取个人信息
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		
		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
	}, 
	
	onShow() {
		/* 当用用户打开此页面时时获取用户 之前加载的信息 */
		this.setData({
			hasUserInfo:app.globalData.hasUserInfo
		})
		if (app.globalData.hasUserInfo) {
			this.setData({
				nickName: app.globalData.userInfo.nickName,
				gender: app.globalData.userInfo.gender,
				userInfo: app.globalData.userInfo,
				avatarUrl:app.globalData.userInfo.avatarUrl,
				hasUserInfo: true
			})
		}
	},
	
	methods: {
		tryLoginWithWechat(){
			if(this.nickName == "未登录"){
				console.log("没有登录")
				uni.navigateTo({
					url:"../../login/login"
				})
			}
		},
		
		setData:function(obj){
		let that = this;    
		let keys = [];    
		let val,data;    
		Object.keys(obj).forEach(function(key){    
		 keys = key.split('.');    
		 val = obj[key];     
		 data = that.$data;    
		 keys.forEach(function(key2,index){    
		     if(index+1 == keys.length){    
		         that.$set(data,key2,val);    
		     }else{    
		         if(!data[key2]){    
		            that.$set(data,key2,{});    
					}    
				}    
		     data = data[key2];    
				})    
			});    
		},
		
		getUserInfo: function(e) {
			// 只在用户点击button时才会触发这个按钮
		        console.log(e)
		        app.globalData.userInfo = e.detail.userInfo
		        this.setData({
		            userInfo: e.detail.userInfo,
		            hasUserInfo: true
		        })
		    },
						
		toMySelfPage(){
			// 条状个人信息主要
			uni.navigateTo({
				url:"../../MySelfPage/MySelfPage"
			})
		},
		
		/* 用于获取app产生的数据 */
		getAppUserInfo(){  
			console.log("用户openid ",app.globalData.openid)
			db.where({
				_openid:app.globalData.openid
			}).get({
				success: this.handleGetAppUserInfoSucc.bind(this)
			}) 
		},
		handleGetAppUserInfoSucc(res){
			console.log("获取app产生的数据 ",res, res.data[0])
			const te = res.data	
			const personInfo = te.map((value, index)=>{
				return{
					nickName:value.nickName,  // 昵称
					age:value.age,			
					sex:value.sex,  
					career:value.career,
					schoolData:value.schoolData,
					provinceSchool:value.provinceSchool,
					longitude:value.longitude, /* 用户当前位置*/
					latitude:value.latitude,
					location:value.location,
				}
			})
			this.personInfo = personInfo ;
			console.log("tap5 用户信息", this.personInfo)			
		},
		fail(err){
			console.log("tap5 用户信息失败。。。",err)
		}
	}
}
